import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import Todos from './components/Todos';


import './App.css';

class App extends Component {

  state = {
    todos: [
      {
        id: 1,
        title: 'Take out the trash',
        completed: false
      },
      {
        id: 2,
        title: 'Update the Opengateweb',
        completed: false
      },
      {
        id: 3,
        title: 'Dinner with wife',
        completed: false
      },
      {
        id: 4,
        title: 'GITHub push the new codo',
        completed: false
      }
    ]
  }

  markComplete = (id) => {
    this.setState({
      todos: this.state.todos.map(todo => {
        if (todo.id === id) {
          todo.completed = !todo.completed
        }
        return todo;
      })
    });
  }

  render() {
    return (
      <Router basename={'/reacttodo'} >
        <Route path={`${process.env.PUBLIC_URL}/reacttodo`} >

          <div className="App">
            <div className="container">

              <div className="row">
                <div className="col s12 m12 l12 m6 z-depth-5">
                  <div className="card blue-grey darken-1">
                    <div className="card-content white-text">
                      <span className="card-title">TODO List</span>
                      <p>With this React Craush Course we will learn about installation and drilling components. No state manager installed - Redux.</p>
                      <p><strong>Work in progress ...</strong></p>


                    </div>
                    <div className="card-action">
                      <a href="https://youtu.be/sBws8MSXN7A">YT Course</a>
                      <a href="https://opengateweb.com">Last min: 50:01</a>

                    </div>
                  </div>
                </div>



              </div>






              <div className="row ">

                <div className="col s12 m12 l12 m6 z-depth-5 mt-30">
                  <div className="card blue-grey lighten-4">
                    <div className="card-content black-text">
                      <span className="card-title">If Completed</span>


                      <Todos todos={this.state.todos} markComplete={this.markComplete} />




                    </div>
                    <div className="card-action ">
                      <a className="black-text" href="https:/opengateweb.com" target="_blank"
                        rel="noopener noreferrer">Home OpenGateWeb</a>
                      <a className="black-text" href="https://reactjs.org/docs/getting-started.html" target="_blank"
                        rel="noopener noreferrer">React Docs</a>

                    </div>
                  </div>
                </div>







              </div>


            </div>
          </div>
        </Route>

      </Router >

    );
  }
}


export default App;
